// React
import React from 'react'

// MUI
import { Avatar, Box, Card, CardActions, CardHeader, Grid, IconButton, LinearProgress, Tooltip } from '@mui/material'

// MUI Icons
import InfoIcon from '@mui/icons-material/Info'
import FolderIcon from '@mui/icons-material/Folder'
import ForwardIcon from '@mui/icons-material/Forward'
import api from '../api'
import { Link } from 'react-router-dom'


function PatientCard(props) {

    const {
        firstName, 
        lastName, 
        dodid
    } = props


    return (
        <Grid item xs={12} md={6} lg={3}>
            <Card>
                <CardHeader
                    avatar={<Avatar><FolderIcon /></Avatar>}
                    title={`${firstName} ${lastName}`}
                    subheader={`${dodid}`}
                />
                <CardActions>
                    <Tooltip
                        title="Show Information"
                    >
                        <IconButton>
                            <InfoIcon fontSize="large" />
                        </IconButton>
                    </Tooltip>
                    <Box sx={{flexGrow: 1}} />
                    <Tooltip
                        title="Open Folder"
                    >
                        <Link to={`/patients/${dodid}`}>
                            <IconButton>
                                <ForwardIcon fontSize="large" />
                            </IconButton>
                        </Link>
                    </Tooltip>
                </CardActions>
            </Card>
        </Grid>
    )
}

function Patients(props) {

    const [loading, setLoading] = React.useState(true)
    const [patients, setPatients] = React.useState([])

    React.useEffect(() => {
        async function load() {
            let p = await api.getPatients()
            setPatients(p)
            setLoading(false)
        }
        if (loading) {
            load()
        }
    }, [loading])

    if (loading) {
        return (
            <LinearProgress />
        )
    }
    return (
        patients.map(patient => <PatientCard key={patient.dodid} {...patient} />)
    )

}

export default Patients