// React
import React from 'react'

// MUI
import { Avatar, Box, Card, CardActions, CardContent, CardHeader, Grid, IconButton, LinearProgress, Tooltip, Typography } from '@mui/material'

// MUI icons
import DescriptionIcon from '@mui/icons-material/Description'
import FileOpenIcon from '@mui/icons-material/FileOpen'

// router
import { useParams } from 'react-router-dom'

// custom
import api from '../api'
import TCCC from '../forms/TCCC'

function PatientData(props) {

    return (
        <Grid item xs={6}>
            <Typography>
                {props.children}
            </Typography>
        </Grid>
    )
}

function PatientInfo(props) {

    const {
        firstName,
        lastName,
        dodid,
        dob,
        bloodType
    } = props

    return (
        <Grid item xs={12}>
            <Card>
                <CardHeader
                    title={`${firstName} ${lastName}`}
                    subheader={`${dodid}`}
                />
                <CardContent>
                    <Grid container spacing={1}>
                        <PatientData>
                            Date of Birth: {dob}
                        </PatientData>
                        <PatientData>
                            Blood Type: {bloodType}
                        </PatientData>
                    </Grid>
                </CardContent>
            </Card>
        </Grid>
    )
}

function DocumentCard(props) {
    const {
        name,
        lastModified,
        data
    } = props

    const [open, setOpen] = React.useState(false)

    // form selection
    let form = null
    if (name === "Tactical Casualty Care Card") {
        form = (
            <TCCC
                open={open}
                close={() => setOpen(false)}
                data={data}
            />
        )
    }

    return (
        <Grid item xs={12} md={6} lg={3}>
            <Card>
                <CardHeader
                    avatar={<Avatar><DescriptionIcon fontSize="large" /></Avatar>}
                    title={name}
                    subheader={`${lastModified.toISOString()}`}
                />
                <CardActions>
                    <Box sx={{ flexGrow: 1 }} />
                    <Tooltip
                        title="View Document"
                    >
                        <IconButton
                            onClick={() => setOpen(true)}
                        >
                            <FileOpenIcon fontSize="large" />
                        </IconButton>
                    </Tooltip>
                </CardActions>
            </Card>
            {form}
        </Grid>
    )
}

function PatientPage(props) {

    const { dodid } = useParams()
    const [loading, setLoading] = React.useState(true)
    const [patient, setPatient] = React.useState({})

    React.useEffect(() => {
        async function load() {
            let p = await api.getPatientById(dodid)
            setPatient(p)
            setLoading(false)
        }
        if (loading) {
            load()
        }
    }, [loading, dodid])

    if (loading) {
        return (<LinearProgress />)
    }

    return (
        <>
            <PatientInfo {...patient} />
            {patient.documents.map(doc => <DocumentCard key={doc.lastModifed} {...doc} />)}
        </>
    )
}

export default PatientPage